.Playlist {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    overflow-y: scroll;
    width: 37%;
    max-height: 965px;
    padding: 0.5rem 1.16rem;
    background-color: rgba(0, 0, 0, 0.9);
/*  box-shadow: 0 4px 2px 2px #000000; */
}

.Playlist input {
    width: 100%;
    border: 0;
    outline: 0;
    background: transparent;
    border-bottom: 1px solid #6f6f6f;
    font-family: 'Nunito', sans-serif;
    font-weight: 600;
    font-size: 1.55rem;
    color: #fff;
}

.Playlist-save {
    cursor: pointer;
    width: 10rem;
    padding: .77rem 0;
    border-radius: 54px;
    margin-top: 1.27rem;
    background-color: #ea8636;
    text-align: center;
    font-size: .83rem;
    -webkit-transition: background-color .25s;
    -o-transition: background-color .25s;
    transition: background-color .25s;
}

.Playlist-save:hover {
    background-color: rgba(192, 96, 20, .7);
}

::-webkit-scrollbar {
    width: 0px;
    background: transparent;
}

@media only screen and (max-width: 1020px) {
    .Playlist {
        width: 90%;
    }
}



